import React from 'react'
import DatePicker, { months } from './Datepicker'
import { makeStyles, Theme } from '@material-ui/core'
import clsx from 'clsx'

const useStyles = makeStyles((theme: Theme) => ({
    container: {
        flexDirection: 'row',
        position: 'relative',
        maxHeight: '40px',
        [theme.breakpoints.down(480)]: {
            width: '100%',
        }
    },
    controlButton: {
        display: 'flex',
        overflow: 'hidden',
        flex: 1,
        padding: '8px 16px',
        boxSizing: 'border-box',
        textAlign: 'center',
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '40px',
        cursor: 'pointer',
        outline: 'none',
        webkitHightlightTapColor: 'transparent',
        fontSize: '16px',
        color: '#fff',
        border: 0,
        background: '#6C5549',
        '&.mainButton': {
            flex: 2,
            borderRight: '.5px solid rgba(0,0,0,.3)',
            borderLeft: '.5px solid rgba(255,255,255,.3)',
            minWidth: '200px'
        },
        '&.rightButton': {
            borderRadius: '0 8px 8px 0',
            borderLeft: '.5px solid rgba(255,255,255,.3)'
        },
        '&.leftButton': {
            borderRadius: '8px 0 0 8px',
            borderRight: '.5px solid rgba(0,0,0,.3)',
        }
    },
    pickerContainer: {
        position: 'absolute',
        zIndex: 1500,
        width: '100%',
        top: 0,
    },
    pickerOutline: {
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
    }
}))

const DateBar: React.FC<{ date?: Date, handleChange: (date: Date) => void }> = ({ date = new Date(), handleChange }) => {
    const classes = useStyles()
    const [ picker, setPicker ] = React.useState<Date | undefined>()
    const go: (n?: number) => void = (n = 1) => {
        const d = new Date(date)

        return handleChange(new Date(d.setDate(d.getDate() + n)))
    }

    const handleDatePickerSelect = ( d: Date ) => {
        console.log('picker selected', d)
        setPicker(undefined)
        return handleChange(d)
    }
    
    return (
        <section className={classes.container}>
            {
                picker && 
                <div className={classes.pickerContainer}>
                    <div className={classes.pickerOutline} onClick={e => setPicker(undefined)} />
                    <DatePicker current={picker} onSelect={handleDatePickerSelect} />
                </div>
            }
            <button className={clsx(classes.controlButton, 'rightButton')} onClick={() => go(-1)}>קודם</button>
            <button className={clsx(classes.controlButton, 'mainButton')} onClick={() => setPicker(date)}>{ date.getDate() } ל{ months[date.getMonth()] }, { date.getFullYear() }</button>
            <button className={clsx(classes.controlButton, 'leftButton')} onClick={() => go()}>הבא</button>
        </section>
    )
}

export default DateBar