import React from 'react'
import LoadingPage from './Loading'
import { useQuery } from '@apollo/client'
import ErrorPage from './Error'
import { AUTH_QUERY } from '../apollo/query'
import HomeView from '../components/HomeView'
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom'
import LoginView from '../components/LoginView'
import ProfileView from '../components/ProfileView'
import AppSettingsView from '../components/AppSettingsView'
import ShiftView from '../components/ShiftView'

export interface IRoute {
    path: string
    name: string
    component: React.FC<any>
    requiresAuth?: true
}

export interface IAuth {
    token: string
    user: {
        id: string
        username: string
        firstname: string
        lastname: string
        avatar: string
        role: string
        [key: string]: string
    }
}

export const hasAdminPermission: (auth: IAuth) => boolean = (auth: IAuth) => {
    const whiteList = [ 'developer', 'מנהל', 'מפתח', 'admin', 'mananger' ]
    return Boolean(whiteList.includes(auth.user.role))
}

const routes: IRoute[] = [
    {
        path: '/',
        name: 'Home',
        component: HomeView,
        requiresAuth: true
    },
    {
        path: '/s/:date?/:kind?',
        name: 'Shift',
        component: ShiftView,
        requiresAuth: true
    },
    {
        path: '/p/:username?',
        name: 'Profile',
        component: ProfileView,
        requiresAuth: true
    },
    {
        path: '/app/settings',
        name: 'Application Settings',
        component: AppSettingsView,
        requiresAuth: true
    },
    {
        path: '/login',
        name: 'Login',
        component: LoginView
    }
]

const AppRouter: React.FC = () => {
    const { loading, error, data } = useQuery(AUTH_QUERY)

    if (loading) return <LoadingPage />

    if (error) return (
        <ErrorPage>
            <p>{error.message}</p>
        </ErrorPage>
    )
    
    const { Authenticate } = data 
    const { user, token } = Authenticate
    const auth: IAuth | undefined = user && token ? Authenticate : undefined

    const AuthRoute: React.FC<{ route: IRoute }> = ({ route }) => {

        if (route.requiresAuth && !auth) return <Redirect to="/login" />

        if (!route.requiresAuth && auth && route.name === 'Login') return <Redirect to="/" />

        if (route.requiresAuth && auth) return <route.component auth={auth} />

        return (
            <route.component />
        )
    } 

    return (
        <Router>
            <Switch>
                {
                    routes.map((route: IRoute, key: number) => (
                        <Route key={key} exact path={route.path}>
                            <AuthRoute route={route} />
                        </Route>
                    ))
                }
            </Switch>
        </Router>
    )
}

export default AppRouter