import React from 'react'
import { IAuth } from '../router';
import ShiftView from './ShiftView';

export interface HomeViewProps {
    auth: IAuth
}

const HomeView: React.FC<{ auth: IAuth }> = ({ auth }) => {
    return (
        <ShiftView auth={auth} />
    )
}

