import React from 'react'
import { LOGIN_MUTATION } from '../apollo/mutation' 
import { 
    Lock as LockIcon,
    LockOutlined as PasswordIcon,
    PersonOutline as IdIcon
} from '@material-ui/icons'
import { OverridableComponent } from '@material-ui/core/OverridableComponent'
import { CircularProgress, makeStyles, Theme, Button } from '@material-ui/core'
import { useMutation } from '@apollo/client'
import clsx from 'clsx'

interface LoginState {
    error?: string | null
    loading?: boolean
    input: {
        id: string
        password: string
    }
}

interface LoginField {
    type: 'text' | 'password' | 'submit'
    name: 'id' | 'password'
    icon: OverridableComponent<any>
    placeholder: string
}

const fields: LoginField[] = [
    {
        type: 'text',
        name: 'id',
        icon: IdIcon,
        placeholder: 'מספר ת"ז'
    },
    {
        type: 'password',
        name: 'password',
        icon: PasswordIcon,
        placeholder: 'סיסמה'
    }
]

const useStyles = makeStyles((theme: Theme) => ({
    container: {
        height: '100%',
    },
    wrapper: {
        maxWidth: '480px',
        width: '100%',
        margin: 'auto auto',
        flexDirection: 'column',
        [theme.breakpoints.down(500)]: {
            padding: '0 20px',
            margin: 'auto 0',
        }
    },
    header: {
        width: '100%',
        alignItems: 'center',
        justifyContent: 'center',
        '& div.lock-icon': {
            '& svg': {
                fontSize: '64px',
                color: '#6c5549'
            }
        },
        '& h1': {
            fontSize: '22px',
            fontWeight: 700,
            letterSpacing: '.0875rem',
            textAlign: 'center',
            color: '#6c5549',
        },
        '& p': {
            color: '#999',
            maxWidth: '80%',
            textAlign: 'center',
            margin: '12px 0',
            fontSize: '16px',
        }
    },
    content: {
        position: 'relative'
    },
    inputField: {
        flexDirection: 'row',
        width: '60%',
        margin: '0 auto',
        justifyContent: 'center',
        height: '60px',
    },
    inputIcon: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        height: '100%',
        marginLeft: '5px',
        '& svg': {
            fontSize: '34px',
            color: '#999'
        },
        '&.active': {
            '& svg': {
                color: '#6c5549'
            }
        }
    },
    input: {
        display: 'flex',
        alignItems: 'center',
        width: '100%',
        fontSize: '18px',
        border: 0,
        borderBottom: '1.5px solid #999',
        background: 0,
        outline: 'none',
        WebkitTapHighlightColor: 'transparent',
        padding: '4px 8px',
        boxSizing: 'border-box',
        '&:focus': {
            borderColor: '#6c5549'
        }
    },
    submitButton: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: '60%',
        minHeight: '40px',
        margin: '12px auto',
        backgroundColor: '#4caf50',
        color: '#fff',
        fontSize: '14px',
        fontWeight: 600,
        userSelect: 'none',
        WebkitTapHighlightColor: 'transparent',
        '&:hover': {
            backgroundColor: '#357a38'
        },
        '&:disabled': {
            opacity: .5
        },
        '& svg': {
            color: '#0089c0'
        }
    },
    errorMessage: {
        position: 'absolute',
        top: '110%',
        width: '100%',
        color: '#f44336',
        fontSize: '16px',
        fontWeight: 400,
        alignItems: 'center'
    }
}))

const LoginView: React.FC = () => {
    const classes = useStyles()
    const [ active, setActive ] = React.useState<'id' | 'password' | null>(null)
    const [ state, setState ] = React.useState<LoginState>({
        input: {
            id: '',
            password: ''
        }
    })

    const [ doLogin ] = useMutation(LOGIN_MUTATION)

    const handleFocus = ( e: React.FocusEvent<HTMLInputElement>) => {
        e.currentTarget.readOnly = false
        setActive(e.currentTarget.name as 'id' | 'password')
    }

    const handleChange = ( e: React.ChangeEvent<HTMLInputElement> ) => {
        if (e.currentTarget.name === 'id' && isNaN(Number(e.currentTarget.value))) {
            return e.preventDefault()
        }
        return setState({ ...state, error: null, input: { ...state.input, [e.currentTarget.name]: e.currentTarget.value }})
    }

    const handleSubmit = ( e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault()

        setState({ ...state, loading: true })

        doLogin({
            variables: {
                input: state.input
            }
        })
        .then(({ data }) => {
            const { Login } = data as { Login: { token: string } }
            const { token } = Login

            if (!token) return setState({ ...state, loading: false, error: 'משהו השתבש יש לרענן את הדף ולנסות שוב' })
            else {
                localStorage.setItem('token', token)
                return window.location.reload()
            }
        })
        .catch(err => {
            setState({ ...state, loading: false, error: err.message })
        })
    }

    return (
        <div className={classes.container}>
            <div className={classes.wrapper}>
                <header className={classes.header}>
                    <div className="lock-icon">
                        <LockIcon />
                    </div>
                    <h1>התחברות</h1>
                    <p>יש להתחבר באמצעות מספר תז וסיסמה, במידה ואחד הפרטים חסר, יש לפנות למנהל.</p>
                </header>
                <main className={classes.content}>
                    <form onSubmit={handleSubmit} autoComplete="off">
                        {
                            fields.map((field: LoginField, i: number) => {
                                return (
                                    <div key={i} className={classes.inputField}>
                                        <label htmlFor={field.name} className={clsx(classes.inputIcon, field.name === active && 'active')}>
                                            <field.icon />
                                        </label>
                                        <div style={{ alignItems: 'center', justifyContent: 'center', height: '60px'}}>
                                            <input readOnly onBlur={() => setActive(null)} onFocus={handleFocus} className={classes.input} type={field.type} placeholder={field.placeholder} value={state.input[field.name]} name={field.name} id={field.name} onChange={handleChange} />
                                        </div>
                                    </div>
                                )
                            })
                        }
                        <div>
                            <Button variant="contained" color="inherit" className={classes.submitButton} type="submit" disabled={ Boolean(state.loading || state.error || !state.input.id || !state.input.password) }>
                                {
                                    state.loading 
                                    ? <div><CircularProgress size={25} /></div>
                                    : 'התחבר'
                                }
                            </Button>
                        </div>
                        {
                            state.error &&
                            <div className={classes.errorMessage}>
                                { state.error }
                            </div>
                        }
                    </form>
                </main>
            </div>
        </div>
    )
}

export default LoginView