import React from 'react'
import { IAuth } from '../router'
import AppNav from './AppNav'
import { Avatar, Theme, makeStyles } from '@material-ui/core'
import { useQuery, useMutation } from '@apollo/client'
import { GET_USER_SHIFTS } from '../apollo/query'
import { weekdays, months } from './Datepicker'
import { Link } from 'react-router-dom'
import { getKindByDate } from './KindBar'
import { SIGN_RECORD_MUTATION } from '../apollo/mutation'

const useStyles = makeStyles((theme: Theme) => ({
    row: {
        flexDirection: 'row',
        '& div': {
            flex: 1,
        }
    }
}))

const ShiftList: React.FC<{ onChange?: () => void, auth: IAuth, items: any, type: 'opened' | 'closed' | 'unsigned' | 'waiting' }> = ({ onChange, auth, items, type }) => {
    const classes = useStyles()
    const [ doSignRecord ] = useMutation(SIGN_RECORD_MUTATION)

    const handleSignRecord = ( d: Date ) => {
        doSignRecord({
            variables: {
                input: { date: d }
            }
        })
        .then(({ data }) => {
            onChange?.()
        })
        .catch(err => {
            console.log(err)
        })
    }

    return (
        <div>
            {
                items.length > 0 &&
                <header className={classes.row}>
                    <div>תאריך</div>
                    <div>משמרת</div>
                    <div>שעות</div>
                    <div>לשעה</div>
                    <div>תשר</div>
                    <div>מקדמה</div>
                    <div>הפרשה/השלמה</div>
                    <div>פעולות</div>
                </header>
            }
            <main>
                {
                    items.length === 0 && <div>אין נתונים</div>
                }
                {
                    items.map((item: any, i: number) => {
                        const date = new Date(item.date)
                        const record = item.records.find((r: any) => r.user.id === auth.user.id)
                        const hours = (new Date(record.end).getTime() - new Date(record.start).getTime() ) / 1000 / 60 / 60
                        let totalHours = 0

                        item.records.forEach((r: any) => {
                            totalHours += (new Date(r.end).getTime() - new Date(r.start).getTime() ) / 1000 / 60 / 60
                        })
                        
                        let perhour = item.tip / totalHours
                        const tip = perhour * hours
                        const discharge = perhour <= 30 ? (hours * 30) - tip : hours * -10
                        const deposit = perhour <= 30 ? tip : tip + discharge

                        const types = [ 'closed', 'unsigned' ]

                        return (
                            <div key={i} className={classes.row}>
                                <div>
                                    { weekdays.long[date.getDay()] }, { date.getDate() } ל{ months[date.getMonth()] }, { date.getFullYear() }
                                </div>
                                <div>
                                    { getKindByDate(date) }
                                </div>
                                <div>
                                    { hours }
                                </div>
                                <div>
                                    { perhour.toFixed(2) }
                                </div>
                                <div>
                                    {
                                        types.includes(type)
                                        ? `${tip.toFixed(2)}`
                                        : '-'
                                    }
                                </div>
                                <div>
                                    {
                                        types.includes(type)
                                        ? `${deposit.toFixed(2)}`
                                        : '-'
                                    }
                                </div>
                                <div>
                                    {
                                        types.includes(type)
                                        ? `${discharge.toFixed(2)}`
                                        : '-'
                                    }
                                </div>
                                <div>
                                    <Link to={`/s/${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}/${getKindByDate(date)}`}>עבור למשמרת</Link>
                                    {
                                        type === 'unsigned' && <button onClick={e => handleSignRecord(date)}>לקחתי</button>
                                    }
                                </div>
                            </div>
                        )
                    })
                }
            </main>
        </div>
    )
}

const ProfileView: React.FC<{ auth: IAuth }> = ({ auth }) => {
    const { loading, error, data, refetch } = useQuery(GET_USER_SHIFTS, {
        fetchPolicy: 'no-cache'
    })

    if (loading ) return <div>טוען</div>

    if (error) return <div>{ error.message }</div>

    const { getUserShifts: shifts } = data || {}
    
    const opened = shifts.filter((r: any) => r.status.opened)
    const closed = shifts.filter((r: any) => r.status.closed && r.records.find((d: any) => d.user.id === auth.user.id && d.signed))
    const unsigned = shifts.filter((r: any) => r.status.signed && r.records.find((d: any) => d.user.id === auth.user.id && !d.signed))
    const waiting = shifts.filter((r: any) => r.status.closed && !r.status.signed && r.records.find((d: any) => d.user.id === auth.user.id))

    console.log({ opened, closed, unsigned })

    let hours = 0, tip = 0, deposit = 0, discharge = 0, completion = 0
    shifts.forEach((shift: any) => {
        shift.hours = 0

        shift.records.forEach((r: any) => {
            r.hours = (new Date(r.end).getTime() - new Date(r.start).getTime() ) / 1000 / 60 / 60

            shift.hours += r.hours
        })
        
        shift.perhour = shift.tip ? shift.tip / shift.hours : 0

        const record = shift.records.find((r: any) => r.user.id === auth.user.id)

        record.tip = shift.perhour * record.hours
        record.discharge = shift.perhour <= 30 ? (record.hours * 30) - record.tip : record.hours * -10
        record.deposit = shift.perhour <= 30 ? record.tip : record.tip + record.discharge

        hours += !shift.status.opened && record.hours
        tip += !shift.status.opened && record.tip
        deposit += !shift.status.opened && record.deposit
        discharge += !shift.status.opened && record.discharge
        completion += !shift.status.opened && record.discharge > 0 ? record.discharge : 0
    })

    const averageHours = hours / shifts.length
    const averagePerHour = tip / hours

    return (
        <AppNav auth={auth}>
            <header>
                <button>
                    <Avatar src={`http://${window.location.hostname}:4000${auth.user.avatar}`} />
                </button>
                <div>
                    <h1>{ auth.user.firstname } { auth.user.lastname }</h1>
                    <p style={{ direction: 'ltr', width: 'auto', textAlign: 'right' }}>@{ auth.user.username }</p>
                </div>
                <div>
                    <button>ערוך פרופיל</button>
                </div>
            </header>
            <main>
                <div>
                    <h3>כללי</h3>
                    <div>
                        <span>סה"כ שעות: </span> { hours }
                    </div>
                    <div>
                        <span>סה"כ משמרות: </span> { shifts.length }
                    </div>
                    <div>
                        <span>סה"כ תשר: </span> { tip.toFixed(2) }
                    </div>
                    <div>
                        <span>סה"כ מקדמה: </span> { deposit.toFixed(2) }
                    </div>
                    <div>
                        <span>סה"כ הפרשות: </span> { discharge.toFixed(2) }
                    </div>
                    <div>
                        <span>סה"כ השלמות: </span> { completion.toFixed(2) }
                    </div>
                    <div>
                        <span>ממוצע שעות יומיות: </span> { averageHours.toFixed(2) }
                    </div>
                    <div>
                        <span>ממוצע תשר לשעה: </span> { averagePerHour.toFixed(2) }
                    </div>
                </div>
                <div>
                    <h3>משמרות פתוחות</h3>
                    <ShiftList auth={auth} items={opened} type="opened" />
                </div>
                <div>
                    <h3>משמרות בהמתנה ללקיחת תשר</h3>
                    <ShiftList onChange={() => refetch()} auth={auth} items={unsigned} type="unsigned" />
                </div>
                <div>
                    <h3>משמרות בהמתנה לאישור מנהל</h3>
                    <ShiftList auth={auth} items={waiting} type="waiting" />
                </div>
                <div>
                    <h3>משמרות סגורות</h3>
                    <ShiftList auth={auth} items={closed} type="closed" />
                </div>
            </main>
        </AppNav>
    )
}

export default ProfileView