import React from 'react'
import { makeStyles, Theme } from '@material-ui/core'
import clsx from 'clsx'

export const months = [ 'ינואר', 'פברואר', 'מרץ', 'אפריל', 'מאי', 'יוני', 'יולי', 'אוגוסט', 'ספטמבר', 'אוקטובר', 'נובמבר', 'דצמבר' ]

export const weekdays = {
    short: [ 'א', 'ב', 'ג', 'ד', 'ה', 'ו', 'שבת' ],
    long: [ 'ראשון', 'שני', 'שלישי', 'רביעי', 'חמישי', 'שישי', 'שבת' ]
}

export const isToday: ( date: Date ) => boolean = ( date ) => {
    const today = new Date()
    return today.getFullYear() === date.getFullYear() && 
        today.getMonth() === date.getMonth() && 
        today.getDate() === date.getDate()
}

export const isSelectedDate: ( a: Date, b: Date ) => boolean = ( a, b ) => {
    return a.getFullYear() === b.getFullYear() &&
        a.getMonth() === b.getMonth() &&
        a.getDate() === b.getDate() 
}

export enum PickerStep {
    DAY = 'DAY',
    MONTH = 'MONTH',
    YEAR = 'YEAR'
}

const useStyles = makeStyles((theme: Theme) => ({
    container: {},
    header: {
        flexDirection: 'row',
        height: '40px',
    },
    controlButton: {
        overflow: 'hidden',
        display: 'flex',
        flex: 1,
        padding: '8px 16px',
        boxSizing: 'border-box',
        textAlign: 'center',
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '40px',
        cursor: 'pointer',
        outline: 'none',
        webkitHightLightTapColor: 'transparent',
        fontSize: '16px',
        color: '#fff',
        border: 0,
        background: '#6C5549',
        '&.mainButton': {
            flex: 2,
            borderRight: '.5px solid rgba(0,0,0,.3)',
            borderLeft: '.5px solid rgba(255,255,255,.3)',
            minWidth: '200px'
        },
        '&.rightButton': {
            borderRadius: '0 8px 0 0',
            borderLeft: '.5px solid rgba(255,255,255,.3)'
        },
        '&.leftButton': {
            borderRadius: '8px 0 0 0',
            borderRight: '.5px solid rgba(0,0,0,.3)',
        }
    },
    main: {
        background: '#fff',
        flexDirection: 'column',
        border: '1px solid #6C5549',
        boxSizing: 'border-box',
        minHeight: '270px',
        borderRadius: '0 0 8px 8px',
        overflow: 'hidden',
        '& button': {
            display: 'flex',
            flex: 1,
            minHeight: '40px',
            alignItems: 'center',
            justifyContent: 'center',
            border: 0,
            background: 0,
            outline: 'none',
            cursor: 'pointer',
        }
    },
    calendarRow: {
        flex: 1,
    },
    dayContainer: {
        '& button': {
            '& div': {
                borderRadius: '50%',
                width: '25px',
                height: '25px',
                alignItems: 'center',
                justifyContent: 'center',
                textAlign: 'center'
            },
            '& div.today': {
                background: '#c4bbb6',
                color: '#6C5549',
                fontWeight: 500
            },
            '& div.selected': {
                background: '#C1E0F7',
                color: '#333',
                fontWeight: 500
            },
            '&:hover': {
                '& div': {
                    background: '#6C5549',
                    color: '#fff',
                }
            }
        },
    },
    weekdayItem: {
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '30px',
        color: '#bbb',
        fontWeight: 500,
        fontSize: '12px',
    }
}))

const DatePicker: React.FC<{ current?: Date, onSelect?: (d: Date) => void }> = ({ current = new Date(), onSelect }) => {
    const classes = useStyles()
    const [ step, setStep ] = React.useState<PickerStep>(PickerStep.DAY)
    const [ date, setDate ] = React.useState<Date>(current)

    const incrementDate = (t: PickerStep, v: number = 1) => {
        let d = new Date(date)
        if (t === PickerStep.DAY) {
            return new Date(d.setDate(d.getDate() + v))
        }
        if (t === PickerStep.MONTH) {
            return new Date(d.setMonth(d.getMonth() + v))
        }
        if (t === PickerStep.YEAR) {
            return new Date(d.setFullYear(d.getFullYear() + v))
        }
        return d
    }

    const handleGo = (d: Date) => {
        console.log('clicked date', d)
        setDate(d)
    }

    const handleSelect = (d: Date) => {
        if (step === PickerStep.DAY) {
            return onSelect?.(d)
        }
        if (step === PickerStep.MONTH) {
            setStep(PickerStep.DAY)
        }
        if (step === PickerStep.YEAR) {
            setStep(PickerStep.MONTH)
        }
        setDate(d)
    }

    const HeaderControls: React.FC = () => {
        let prev = { text: '', date }, curr = { text: '', date }, next = { text: '', date }
        if (step === PickerStep.DAY) {
            prev = { text: months[incrementDate(PickerStep.MONTH, -1).getMonth()], date: incrementDate(PickerStep.MONTH, -1) }
            curr = { text: months[new Date(date).getMonth()], date }
            next = { text: months[incrementDate(PickerStep.MONTH).getMonth()], date: incrementDate(PickerStep.MONTH) }
        }
        if (step === PickerStep.MONTH) {
            prev = { text: incrementDate(PickerStep.YEAR, -1).getFullYear().toString(), date: incrementDate(PickerStep.YEAR, -1) }
            curr = { text: new Date(date).getFullYear().toString(), date }
            next = { text: incrementDate(PickerStep.YEAR).getFullYear().toString(), date: incrementDate(PickerStep.YEAR) }
        }
        if (step === PickerStep.YEAR) {
            prev = { text: incrementDate(PickerStep.YEAR, -5).getFullYear().toString(), date: incrementDate(PickerStep.YEAR, -5) }
            curr = { text: `${(new Date(date).getFullYear() - 4).toString()}-${(new Date(date).getFullYear() + 4).toString()}`, date }
            next = { text: incrementDate(PickerStep.YEAR, 5).getFullYear().toString(), date: incrementDate(PickerStep.YEAR, 5) }
        }

        const handleStepChange = () => {
            let s = PickerStep.DAY
            if (step === PickerStep.DAY) s = PickerStep.MONTH
            if (step === PickerStep.MONTH) s = PickerStep.YEAR
            if (step === PickerStep.YEAR) return
            return setStep(s)
        }

        return (
            <header className={classes.header}>
                <button className={clsx(classes.controlButton, 'rightButton')} onClick={() => handleGo(prev.date)}>{ step === PickerStep.DAY ? prev.text.substr(0,3) : prev.text }</button>
                <button className={clsx(classes.controlButton, 'mainButton')} onClick={handleStepChange}>{ curr.text }</button>
                <button className={clsx(classes.controlButton, 'leftButton')} onClick={() => handleGo(next.date)}>{ step === PickerStep.DAY ? next.text.substr(0,3) : next.text }</button>
            </header>
        )
    }

    const PickerBody: React.FC = () => {
        let elems: any = []
        const d = new Date(date)
        if (step === PickerStep.DAY) {
            const startWeekDay = new Date(d.setDate(1)).getDay()
            const monthDays = new Date(d.getFullYear(), d.getMonth() + 1, 0).getDate()
            
            for (let i = startWeekDay-1; i >= 0; i--) {
                elems.push({ elem: <div className="prevMonth" key={`prev_month_${i}`}>{ monthDays-i }</div>, date: new Date(d.getFullYear(), d.getMonth() - 1, monthDays-i) })
            }
            for (let i = 1; i <= monthDays; i++) {
                elems.push({ elem: <div className={clsx(isToday(new Date(d.setDate(i))) && 'today', isSelectedDate(new Date(current), new Date(d.setDate(i))) && 'selected')} key={`curr_month_${i}`}>{i}</div>, date: new Date(d.setDate(i)) })
            }
            const l = 42 - elems.length
            for (let i = 1; i <= l; i++) {
                elems.push({ elem: <div className="nextMonth" key={`next_month_${i}`}>{i}</div>, date: new Date(d.getFullYear(), d.getMonth() + 1, i) })
            }

            elems = elems.concat.apply([], 
                elems.map((e: any, i: number) => {
                    return i % 7 ? [] : [ elems.slice(i, i+7) ]
                })    
            )
        }

        if (step === PickerStep.MONTH) {
            months.forEach((m: string, i: number) => {
                elems.push({ elem: <div key={`month_${i}`}>{ months[i] }</div>, date: d.setMonth(i) })
            })

            elems = elems.concat.apply([], 
                elems.map((e: any, i: number) => {
                    return i % 3 ? [] : [ elems.slice(i, i+3) ]
                })    
            )
        }

        if (step === PickerStep.YEAR) {
            for (let i = d.getFullYear() - 4; i < (d.getFullYear() + 5); i++) {
                elems.push({ elem: <div key={`year_${i}`}>{ i }</div>, date: new Date(i, d.getMonth(), d.getDate()) })
            }

            elems = elems.concat.apply([], 
                elems.map((e: any, i: number) => {
                    return i % 3 ? [] : [ elems.slice(i, i+3) ]
                })    
            )
        }

        return (
            <main className={clsx(classes.main, step === PickerStep.DAY && classes.dayContainer )}>
                {
                    step === PickerStep.DAY &&
                    <div style={{flexDirection: 'row'}}>
                        {
                            weekdays.short.map((day: string, i: number) => (
                                <div className={classes.weekdayItem} key={i}>{ day }</div>
                            ))
                        }
                    </div>
                }
                { 
                    elems.map((row: any, i: number) => (
                        <div className={classes.calendarRow} key={i} style={{flexDirection: 'row'}}>
                            {
                                row.map((item: any, i: number) => (
                                    <button onClick={e => handleSelect(item.date)} key={i}>{ item.elem }</button>
                                ))
                            }
                        </div>
                    ))
                }
            </main>
        )
    }

    return (
        <div className={classes.container}>
            <HeaderControls />
            <PickerBody />
        </div>
    )
}

export default DatePicker