import React from 'react'
import { IAuth } from '../router'
import AppNav from './AppNav'
import { useParams, useHistory } from 'react-router-dom'
import { useQuery, useMutation } from '@apollo/client'
import { GET_SHIFT } from '../apollo/query'
import { isToday, months, weekdays } from './Datepicker'
import DateBar from './Datebar'
import ShiftTable, { withZero, useFloatBoxStyles } from './ShiftTable'
import KindBar, { Kind, getKindByDate } from './KindBar'
import { makeStyles, Theme, useMediaQuery } from '@material-ui/core'
import { CLOSE_SHIFT_MUTATION, SIGN_SHIFT_MUTATION } from '../apollo/mutation'
import { RECORD_ADDED_SUBSCRIPTION } from '../apollo/subscriptions'

interface ShiftViewParams {
    date?: Date
    kind?: Kind
}

const useStyles = makeStyles((theme: Theme) => ({
    header: {
        flexDirection: 'row',
        flexWrap: 'wrap',
        justifyContent: 'space-between',
        padding: '20px 0',
        [theme.breakpoints.down(521)]: {
            flexDirection: 'column'
        },
    },
    main: {
        marginBottom: '20px'
    },
    closeShiftButton: {
        background: '#f44336',
        color: '#fff',
        fontWeight: 500,
        border: 0,
        display: 'flex',
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
        borderRadius: '8px',
        marginRight: '10px',
        padding: '0 10px',
        boxSizing: 'border-box',
        cursor: 'pointer',
        outline: 'none',
        WebkitTapHighlightColor: 'transprent'
    },
    signShiftButton: {
        background: '#4caf50',
        color: '#fff',
        fontWeight: 500,
        border: 0,
        display: 'flex',
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
        borderRadius: '8px',
        marginRight: '10px',
        padding: '0 10px',
        boxSizing: 'border-box',
        cursor: 'pointer',
        outline: 'none',
        WebkitTapHighlightColor: 'transprent'
    }
}))

const CloseShiftButton: React.FC<{ date: Date }> = ({ date }) => {
    const classes = useStyles()
    const boxClasses = useFloatBoxStyles()
    const [ open, setOpen ] = React.useState<boolean>(false)
    const [ tip, setTip ] = React.useState<number>(0)

    const [ doClose ] = useMutation(CLOSE_SHIFT_MUTATION)

    const handleSubmit = ( e?: React.MouseEvent ) => {
        doClose({
            variables: {
                input: {
                    date,
                    tip
                }
            }
        })
        .then(({ data }) => {
            console.log(data)
            setOpen(false)
        })
        .catch(err => {
            console.log(err)
        })
    }

    return (
        <>
            {
                open &&
                <div style={{ zIndex: 1499, position: 'fixed', width: '100%', height: '100%', top: 0, left: 0 }}>
                    <div className={boxClasses.container}>
                        <div onClick={e => setOpen(false)} className={boxClasses.outline}></div>
                        <div className={boxClasses.box}>
                            <header className={boxClasses.header}>
                                <h3>סגירת משמרת</h3>
                                <span>{ date.getDate() } ל{ months[date.getMonth()] }, { date.getFullYear() }</span>
                            </header>
                            <main className={boxClasses.main}>
                                <h4>הזן סכום תשר</h4>
                                <input type="number" value={tip.toString()} onChange={e => setTip(Number(e.currentTarget.value))} />
                            </main>
                            <footer className={boxClasses.footer}>
                                <button onClick={handleSubmit}>אישור</button>
                                <button onClick={e => setOpen(false)}>בטל</button>
                            </footer>
                        </div>
                    </div>
                </div>
            }
            <div>
                <button className={classes.closeShiftButton} onClick={e => setOpen(true)}>סגור משמרת</button>
            </div>
        </>
    )
}

const SignShiftButton: React.FC<{ date: Date }> = ({ date }) => {
    const classes = useStyles()
    const [ doClose, { loading } ] = useMutation(SIGN_SHIFT_MUTATION)

    const handleClick = ( e?: React.MouseEvent ) => {
        doClose({
            variables: {
                input: { date }
            }
        })
        .then(({ data }) => {
            console.log(data)
        })
        .catch(err => {
            console.log(err)
        })
    }

    return (
        <>
            <div>
                <button className={classes.signShiftButton} disabled={loading} onClick={handleClick}>אישור מנהל</button>
            </div>
        </>
    )
}

export const DateTimeString: React.FC<{ date?: Date }> = ({ date = new Date() }) => {
    
    return (
        <time dateTime={date.toLocaleString()}>
            { withZero(date.getHours()) }:{ withZero(date.getMinutes()) }, { weekdays.long[ date.getDay() ]}, { date.getDate() } ל{ months[date.getMonth() ]} { date.getFullYear() }
        </time>
    )
}

const ShiftView: React.FC<{ auth: IAuth }> = ({ auth }) => {
    const smallScreen = useMediaQuery('(max-width: 520px)')
    const classes = useStyles()
    const history = useHistory()
    const params: ShiftViewParams = useParams() as ShiftViewParams
    let { date, kind } = params

    date = date ? new Date(date) : new Date()
    kind = kind ? kind : getKindByDate(date)
    
    const { loading, error, data, refetch, subscribeToMore } = useQuery(GET_SHIFT, {
        variables: {
            date: new Date(date.getFullYear(), date.getMonth(), date.getDate(), kind === Kind.MORNING ? 9 : 16, 0, 0)
        },
        fetchPolicy: 'no-cache'
    })

    subscribeToMore({
        document: RECORD_ADDED_SUBSCRIPTION,
        updateQuery: (prev, { subscriptionData }) => {
            refetch()
        }
    })
    
    const todayKind = getKindByDate(new Date())

    const handleDateChange = ( date: Date ) => {
        const dateURI = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
        if (isToday(date) && todayKind === kind) return history.push('/')

        return history.push(`/s/${dateURI}/${kind}`)
    }

    const handleKindChange = ( kind: Kind ) => {
        const d = date || new Date()
        const dateURI = `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()}`

        if (isToday(d) && todayKind === kind) return history.push('/')

        return history.push(`/s/${dateURI}/${kind}`)
    }

    const { getShift: shift } = data ? data : { getShift: null }

    const needSign = !loading && data ? (auth.user.role === 'מנהל' || auth.user.role === 'מפתח') && !Boolean(shift.status.signed) && Boolean(shift.status.closed) : false
    const needClose = !loading && data && !Boolean(shift.status.closed) && shift.isInShift

    const PrivacyCheck = () => {
        if (loading || error || !data) return <div>טוען</div> 
        if (shift.status.closed && !shift.isInShift && auth.user.role === 'מלצר') return (
            <div>
                <h3>משמרת סגורה</h3>
                <p>אין אפשרות לצפות במשמרת כיוון שאינך נמצא בה</p>
            </div>
        )

        const closed = shift.status.closed ? { ...shift.status.closed, date: new Date(shift.status.closed.date) } : null
        const signed = shift.status.signed ? { ...shift.status.signed, date: new Date(shift.status.signed.date) } : null
        
        return (
            <div>
                {
                    shift.status.closed && 
                    <div>
                        <h3>משמרת סגורה</h3>
                        {
                            closed &&
                            <p>
                                נסגרה על ידי <b>{ closed.user.firstname } { closed.user.lastname }</b> <DateTimeString date={closed.date} />
                            </p>
                        }
                        {
                            signed && 
                            <p>
                                אושרה על ידי <b>{ signed.user.firstname } { signed.user.lastname }</b> <DateTimeString date={signed.date} />
                            </p>
                        }
                    </div>
                }
                <ShiftTable auth={auth} loading={loading} error={error} data={data} />
            </div>
        )
    }

    return (
        <AppNav auth={auth}>
            <header className={classes.header}>
                <DateBar handleChange={handleDateChange} date={date} />
                <div style={{ position: 'relative', marginTop: smallScreen ? '10px' : 0, width: smallScreen ? '100%' : 'auto', flexDirection: 'row', justifyContent: 'space-between' }}>
                    <KindBar kind={kind} handleChange={handleKindChange} />
                    {
                        needSign && <SignShiftButton date={new Date(shift.date)} />
                    }
                    {
                        needClose && <CloseShiftButton date={new Date(shift.date)} />
                    }
                </div>
            </header>
            <main className={classes.main}>
                <PrivacyCheck />
            </main>
        </AppNav>
    )
}

export default ShiftView