import React from 'react'
import { IAuth, hasAdminPermission } from '../router'
import { Avatar, makeStyles, Theme } from '@material-ui/core'
import { Link } from 'react-router-dom'
import {
    ArrowDropDownRounded as ArrowDownIcon,
    ArrowDropUpRounded as ArrowUpIcon,
    PersonRounded as ProfileIcon,
    ExitToAppRounded as LogoutIcon,
    Settings as SettingsIcon,
    SettingsApplicationsRounded as AppSettingsIcon
} from '@material-ui/icons'
import AppLogo from './AppLogo'
import clsx from 'clsx'
import { OverridableComponent } from '@material-ui/core/OverridableComponent'

interface MenuItem {
    text: string
    icon: OverridableComponent<any>
    link?: string
    button?: {
        onClick: () => void
    },
    color?: 'red'
}

const useStyles = makeStyles((theme: Theme) => ({
    container: {
        height: '100%',
    },
    wrapper: {
        maxWidth: '980px',
        width: '100%',
        margin: '0 auto',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        [theme.breakpoints.down(1020)]: {
            padding: '0 20px',
            margin: 0,
        }
    },
    header: {
        height: '70px',
        flexDirection: 'row',
        alignItems: 'center',
        background: '#fff',
        boxShadow: '0 1px 2px rgba(0,0,0,.1)',
        boxSizing: 'border-box',
    },
    main: {
        position: 'relative',
        zIndex: 1000,
        maxWidth: '980px',
        width: '100%',
        height: 'calc(100% - 70px)',
        boxSizing: 'border-box',
        margin: '0 auto',
        flexDirection: 'row',
        [theme.breakpoints.down(1020)]: {
            padding: '0 20px',
            margin: 0,
        }
    },
    userButton: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'flex-end',
        boxSizing: 'border-box',
        padding: '4px 8px',
        border: 0,
        background: 0,
        outline: 'none',
        userSelect: 'none',
        userDrag: 'none',
        WebkitTapHighlightColor: 'transparent',
        fontSize: '16px',
        fontWeight: 500,
        width: '130px',
        transition: 'width 0.1s ease-in-out',
        '&.active': {
            width: '200px',
            justifyContent: 'space-between',
            borderRadius: '10px 10px 0 0',
            boxShadow: '0 0px 3px rgba(0,0,0,.3)'
        }
    },
    userBubble: {
        position: 'relative'
    },
    menuContainer: {
        position: 'absolute',
        zIndex: 1101,
        top: '100%',
        width: '100%',
        background: '#fff',
        boxShadow: '0 0 3px rgba(0,0,0,.3)',
        borderRadius: '0 0 10px 10px',
        '&::before': {
            content: '""',
            position: 'absolute',
            top: '-3px',
            width: '100%',
            height: '3px',
            background: '#fff'
        },
    },
    menuBg: {
        position: 'fixed',
        width: '100%',
        height: '100%',
        top: 0,
        left: 0,
        zIndex: 1100
    },
    menuBox: {
        zIndex: 1101,
        paddingBottom: '10px',
        boxSizing: 'border-box'
    },
    menuItem: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        cursor: 'pointer',
        background: 0,
        border: 0,
        padding: '6px 12px',
        boxSizing: 'border-box',
        textDecoration: 'none',
        userSelect: 'none',
        userDrag: 'none',
        WebkitTapHighlightColor: 'transparent',
        outline: 'none',
        fontWeight: 500,
        fontSize: '14px',
        lineHeight: '30px',
        color: '#333',
        '&:hover': {
            backgroundColor: 'rgba(0,0,0,.0425)',
            color: '#0089c0'
        },
        '& div.icon': {
            marginLeft: '10px',
            height: '40px',
            width: '35px',
            alignItems: 'center',
            justifyContent: 'center',
        },
        '&.primaryButton': {
            color: '#f44336'
        }
    }
}))

const AppNav: React.FC<{ children: JSX.Element | JSX.Element[], auth: IAuth }> = ({ children, auth }) => {
    const classes = useStyles()
    const [ menu, setMenu ] = React.useState<boolean>(false)

    const openMenu = () => setMenu(true)
    const closeMenu = () => setMenu(false)

    const handleLogout = () => {
        localStorage.removeItem('token')
        window.location.reload()
    }

    const DropDownMenu: React.FC = () => {
        const items: MenuItem[] = [
            {
                text: 'פרופיל',
                link: `/p/${auth.user.username}`,
                icon: ProfileIcon
            },
            {
                text: 'הגדרות',
                link: '/account/settings',
                icon: SettingsIcon
            },
            {
                text: 'התנתק',
                button: {
                    onClick: handleLogout
                },
                icon: LogoutIcon,
                color: 'red'
            }
        ]

        if (hasAdminPermission(auth)) {
            items.unshift({
                text: 'הגדרות מערכת',
                link: '/app/settings',
                icon: AppSettingsIcon,
                color: 'red'
            })
        }

        return (
            <div className={classes.menuContainer}>
                <div className={classes.menuBg} onClick={closeMenu} />
                <div className={classes.menuBox}>
                    {
                        items.map((item: MenuItem, i: number) => {
                            return (
                                <div key={i}>
                                    {
                                        item.link &&
                                        <Link key={i} className={classes.menuItem} to={item.link}>
                                            <div className="icon">
                                                <item.icon />
                                            </div>
                                            { item.text }
                                        </Link>
                                    }
                                    {
                                        item.button &&
                                        <button className={clsx(classes.menuItem, item.color && 'primaryButton')} onClick={item.button.onClick}>
                                            <div className="icon">
                                                <item.icon />
                                            </div>
                                            { item.text }
                                        </button>
                                    }
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }

    return (
        <div className={classes.container}>
            <header className={classes.header}>
                <div className={classes.wrapper}>
                    <div style={{ maxWidth: '150px'}}>
                        <AppLogo />
                    </div>
                    <div className={classes.userBubble}>
                        <button className={clsx(classes.userButton, menu && 'active')} onClick={menu ? closeMenu : openMenu}>
                            <div style={{ flexDirection: 'row', alignItems: 'center' }}>
                                <div>
                                    <Avatar src={`http://${window.location.hostname}:4000${auth.user.avatar}`} />
                                </div>
                                <div style={{ marginRight: '10px' }}>
                                    { auth.user.firstname }
                                </div>
                            </div>
                            <div style={{ alignItems: 'center', justifyContent: 'center', height: '40px', marginTop: '2px' }}>
                                {
                                    menu 
                                    ? <ArrowUpIcon />
                                    : <ArrowDownIcon />
                                }
                            </div>
                        </button>
                        {
                            menu && <DropDownMenu />
                        }
                    </div>
                </div>
            </header>
            <main className={classes.main}>
                <div style={{ width: '100%' }}>
                    { children }
                </div>
            </main>
        </div>
    )
}

export default AppNav