import React from 'react'
import { IAuth, hasAdminPermission } from '../router'
import AppNav from './AppNav'
import { makeStyles, Theme } from '@material-ui/core'
import {
    ExpandMoreRounded as ArrowDownIcon,
    ExpandLessRounded as ArrowUpIcon
} from '@material-ui/icons'
import clsx from 'clsx'
import { AddAccount, UsersList, DeleteUser, UpdateAccount } from './AppSettingsItems'

const useStyles = makeStyles((theme: Theme) => ({
    container: {
        minHeight: 'calc(100% - 40px)',
        boxSizing: 'border-box',
        margin: '20px 0',
    },
    item: {
        marginBottom: '12px',
        maxHeight: '40px',
        width: '100%',
        border: '1px solid #eee',
        background: '#fff',
        overflow: 'hidden',
        borderRadius: '8px',
        transition: 'max-height 0.5s linear',
        '&.opened': {
            maxHeight: '100%',
        }
    },
    itemHeader: {
        height: '40px',
        width: '100%',
        background: '#eee',
        '& button': {
            padding: '4px 12px',
            boxSizing: 'border-box',
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'space-between',
            fontSize: '16px',
            fontWeight: 500,
            width: '100%',
            height: '100%',
            display: 'flex',
            border: 0,
            background: 0,
            cursor: 'pointer',
            outline: 'none',
            WebkitTapHighlightColor: 'transparent',
        }
    },
    itemContent: {
        minHeight: '40px',
    }
}))

const AppSettingsView: React.FC<{ auth: IAuth }> = ({ auth }) => {
    const classes = useStyles()
    const [ open, setOpen ] = React.useState<string | null>(null)
    
    if (!hasAdminPermission(auth)) {
        return (
            <AppNav auth={auth}>
                <div>
                    <h1>גישה נדחתה!</h1>
                    <p>אין לך הרשאות לצפייה בדף זה</p>
                </div>
            </AppNav>
        )
    }

    const SettingsItem: React.FC<{ children: JSX.Element | JSX.Element[], title: string }> = ({ children, title }) => {
        return (
            <div className={clsx(classes.item, open === title && 'opened')}>
                <header className={classes.itemHeader}>
                    <button onClick={() => setOpen(open === title ? null : title)}>
                        <h3>{ title }</h3>
                        <div>
                            {
                                open === title 
                                ? <ArrowUpIcon />
                                : <ArrowDownIcon />
                            }
                        </div>
                    </button>
                </header>
                {
                    open === title &&
                    <div className={classes.itemContent}>
                        { children }
                    </div>
                }
            </div>
        )
    }

    return (
        <AppNav auth={auth}>
            <div className={classes.container}>
                <SettingsItem title="הוספת משתמש">
                    {
                        open === 'הוספת משתמש'
                        ? <AddAccount onClose={() => setOpen(null)} />
                        : <div></div>
                    }
                </SettingsItem>
                <SettingsItem title="משתמשים">
                    <UsersList opened={Boolean(open === 'משתמשים')} onClose={() => setOpen(null)} />
                </SettingsItem>
                <SettingsItem title="עדכון משתמש">
                    <UpdateAccount opened={Boolean(open === 'עדכון משתמש')} onClose={() => setOpen(null)} />
                </SettingsItem>
                <SettingsItem title="מחיקת משתמש">
                    <DeleteUser opened={Boolean(open === 'מחיקת משתמש')} onClose={() => setOpen(null)} />
                </SettingsItem>
            </div>
        </AppNav>
    )
}

export default AppSettingsView