import React from 'react'
import { Link } from 'react-router-dom'
import { makeStyles, Theme } from '@material-ui/core'

const useStyles = makeStyles((theme: Theme) => ({
    container: {
        maxWidth: '205px',
        maxHeight: '69px',
        width: '100%',
        height: '100%',
        background: '#fff',
        userSelect: 'none',
    },
    link: {
        position: 'relative',
        textDecoration: 'none',
        cursor: 'pointer',
        color: '#6c5549',
        fontSize: '2vh',
        userDrag: 'none'
    },
    image: {
        width: '100%',
        height: '100%',
        objectFit: 'contain',
        userDrag: 'none',
    },
    text: {
        position: 'absolute',
        bottom: '-.2vh',
        width: '67%',
        height: '20px',
        right: '16%',
        background: '#fff',
        textAlign: 'center',
        fontWeight: 700,
        letterSpacing: '.08rem',
        userDrag: 'none',
        fontFamily: 'Tahoma'
    }
}))

const AppLogo: React.FC = () => {
    const { container, link, image, text } = useStyles()
    return (
        <div className={container}>
            <Link to="/" className={link}>
                <img className={image} src="/gordo-logo.png" alt="לוגו" />
                <span className={text}>ניהול טיפים</span>
            </Link>
        </div>
    )
}

export default AppLogo