import { useMediaQuery, makeStyles, Theme } from '@material-ui/core'
import { Kind, getKindByDate } from './KindBar'
import { months } from './Datepicker'
import { ADD_RECORD_MUTATION, SIGN_RECORD_MUTATION } from '../apollo/mutation'
import { useMutation } from '@apollo/client'
import { IAuth } from '../router'

interface AddRecordState {
    date: Date
    kind: Kind
    start: Date
    end: Date
    [key: string]: Kind | Date
}

export const withZero: (v: number) => string = (v: number) => v < 10 ? `0${v}` : v.toString()

const getTotalHours: (a: Date, b: Date) => number = (a: Date, b: Date) => {
    return (b.getTime() - a.getTime()) / 1000 / 60 / 60
}

