const AddRecordBox: React.FC<{ shift: { kind: Kind, date: Date }, onSubmit?: () => void, onCancel?: () => void }> = ({ shift, onSubmit, onCancel }) => {
    const classes = useFloatBoxStyles()
    const [ state, setState ] = React.useState<AddRecordState>({
        date: new Date(shift.date),
        kind: shift.kind,
        start: new Date(shift.date),
        end: new Date(shift.date.setHours(shift.date.getHours() + 1))
    })

    const [ doAddRecord ] = useMutation(ADD_RECORD_MUTATION)

    const TimeSelect: React.FC<{ target: 'start' | 'end' }> = ({ target }) => {
        const classes = useStyles()
        const he = { start: 'התחלה', end: 'סיום' }

        const incrementDate: (d: Date, t: 'h' | 'm', v?: number) => Date = (d, t, v = 1) => {
            d = new Date(d)
            if (t === 'm') return new Date(d.setMinutes(d.getMinutes() + (v * 15))) 
            return new Date(d.setHours(d.getHours() + v))
        }

        const handleIncrement = ( d: Date ) => {
            setState({ ...state, [target]: d })
        }

        return (
            <div className={classes.timeSelectContainer}>
                <h4 className={classes.timeSelectTitle}>{ he[target] }</h4>
                <div className={classes.timeSelectItems} style={{ flexDirection: 'row-reverse', alignItems: 'center' }}>
                    <div className={classes.timeSelectItem}>
                        <button onClick={e => handleIncrement(incrementDate(state[target], 'h', -1)) }>{ withZero(incrementDate(state[target], 'h', -1).getHours()) }</button>
                        <div>{ withZero(state[target].getHours()) }</div>
                        <button onClick={e => handleIncrement(incrementDate(state[target], 'h')) }>{ withZero(incrementDate(state[target], 'h').getHours()) }</button>
                    </div>
                    <span>:</span>
                    <div className={classes.timeSelectItem}>
                        <button onClick={e => handleIncrement(incrementDate(state[target], 'm', -1)) }>{ withZero(incrementDate(state[target], 'm', -1).getMinutes()) }</button>
                        <div>{ withZero(state[target].getMinutes()) }</div>
                        <button onClick={e => handleIncrement(incrementDate(state[target], 'm')) }>{ withZero(incrementDate(state[target], 'm').getMinutes()) }</button>
                    </div>
                </div>
            </div>
        )
    }
    
    const handleSubmit = () => {
        doAddRecord({
            variables: {
                input: state
            }
        })
        .then(({ data }) => {
            onSubmit?.()
        })
        .catch(err => {
            console.log(err)
        })
    }

    let error = ''
    let h = getTotalHours(state.start,state.end)
    if (h === 0) error = 'שעת סיום זהה לשעת התחלה'
    if (h < 0) error = 'שעת סיום קטנה יותר משעת התחלה'

    return (
        <div className={classes.container}>
            <div onClick={e => onCancel?.()} className={classes.outline}></div>
            <div className={classes.box}>
                <header className={classes.header}>
                    <h3>הוספת רישום</h3>
                    <span>
                        { state.date.getDate() } ל{ months[ state.date.getMonth() ]}, { state.date.getFullYear() }, { getKindByDate(state.date) }
                    </span>
                </header>
                <main className={classes.main} style={{ flexDirection: 'row', alignItems: 'flex-start' }}>
                    <TimeSelect target="start" />
                    <TimeSelect target="end" />
                </main>
                <footer className={classes.footer}>
                    <h4 className={classes.footerItem}>
                        <span>סה"כ שעות:</span> { getTotalHours(state.start, state.end) }
                    </h4>
                    <div style={{ color: 'red'}} className={classes.footerItem}>{ error }</div>
                    <div className={classes.footerControls} style={{ flexDirection: 'row' }}>
                        <button className="confirmButton" disabled={Boolean(error)} onClick={handleSubmit}>אישור</button>
                        <button className="cancelButton" onClick={e => onCancel?.() }>בטל</button>
                    </div>
                </footer>
            </div>
        </div>
    )
}

const ShiftTable: React.FC<{ auth: IAuth, loading: boolean, error: any, data: any }> = ({ auth, loading, error, data }) => {
    const classes = useStyles()
    const [ addRecordBox, setAddRecordBox ] = React.useState<{ kind: Kind, date: Date } | undefined>()
    const [ doSignRecord] = useMutation(SIGN_RECORD_MUTATION)
    const smallScreen = useMediaQuery('(max-width: 768px)')
    if (loading) return <div>loading</div>

    if (error) return <div>{ error.message }</div>

    const { getShift: shift } = data || {}

    if (shift) {
        shift.hours = 0
        shift.workers = 0
        shift.records.forEach((rec: any) => {
            rec.start = new Date(rec.start)
            rec.end = new Date(rec.end)
            rec.hours = (rec.end.getTime() - rec.start.getTime()) / 1000 / 60 / 60

            shift.hours += rec.hours
            shift.workers++
        })

        shift.perhour = shift.tip ? Number((shift.tip / shift.hours).toFixed(2)) : 0
        shift.deposit = shift.perhour <= 30 ? 0 : shift.hours * 10
    }

    const leadZero: (n: number) => string = (n) => n < 10 ? `0${n}` : n.toString()

    console.log(shift)

    const handleAddRecordOpen = ( e?: React.MouseEvent ) => {
        setAddRecordBox({ date: new Date(shift.date), kind: getKindByDate(new Date(shift.date)) })
    }

    const handleSignRecord = () => {
        doSignRecord({
            variables: { 
                input: { date: shift.date }
             }
        })
        .then(({ data }) => {
            console.log(data)
        })
        .catch(err => {
            console.log(err)
        })
    }

    const headers: { [key: string]: { text: string, flex?: number, value: (r: any) => string | JSX.Element } } = {
        index: {
            text: '#',
            value: (r) => shift.records.indexOf(r) + 1
        },
        firstname: {
            text: 'שם פרטי',
            flex: 2,
            value: (r) => r.user.firstname
        },
        lastname: {
            text: 'שם משפחה',
            flex: 2,
            value: (r) => r.user.lastname
        },
        id: {
            text: 'מספר תז',
            flex: 2,
            value: (r) => r.user.id
        },
        start: {
            text: 'התחלה',
            value: (r) => `${ leadZero(r.start.getHours()) }:${ leadZero(r.start.getMinutes()) }`
        },
        end: {
            text: 'סוף',
            value: (r) => `${ leadZero(r.end.getHours()) }:${ leadZero(r.end.getMinutes()) }`
        },
        hours: {
            text: 'שעות',
            value: (r) => r.hours
        },
        tip: {
            text: 'תשר',
            value: (r) => shift.perhour ? (r.hours * shift.perhour).toFixed(2) : ''
        },
        discount: {
            text: 'מקדמה',
            value: (r) => shift.perhour ? shift.perhour > 30 ? ((r.hours * shift.perhour) - (r.hours * 10)).toFixed(2) : (r.hours * shift.perhour).toFixed(2) : ''
        },
        deposit: {
            text: 'הפרשה',
            value: (r) => shift.perhour ? shift.perhour > 30 ? (r.hours * 10).toFixed(2) : ((r.hours * 30) - (r.hours * shift.perhour)).toFixed(2) : ''
        },
        sign: {
            text: 'סטאטוס',
            value: (r) => r.signed ? 'נלקח' : shift.status.signed && r.user.id === auth.user.id ? <button className={classes.signRecordButton} onClick={handleSignRecord}>לקחתי</button> : ''
        }
    }

    const totals: { [key: string]: string } = {
        tip: 'סה"כ תשר',
        hours: 'סה"כ שעות',
        perhour: 'תשר לשעה',
        deposit: 'סה"כ הפרשה'
    }

    if (smallScreen) return (
        <>
            {
                addRecordBox && <AddRecordBox shift={addRecordBox} onSubmit={() => setAddRecordBox(undefined)} onCancel={() => setAddRecordBox(undefined)} />
            }
            <div style={{ width: '100%', flex: 1 }}>
                {
                    shift.records.length === 0 && <h3>אין רישומים למשמרת זו</h3>
                }
                {
                    !shift.isInShift && !shift.status.closed && <button className={classes.addRecordButton} onClick={handleAddRecordOpen}>הוסף רישום</button>
                }
            </div>
            <div style={{ flexDirection: 'row', flexWrap: 'wrap' }}>
                {
                    shift.records.map((record: any, ri: number) => (
                        <div key={ri} style={{ flex: 1, margin: '10px 0 0', minWidth: '280px', width: '100%', boxSizing: 'border-box', borderRadius: '8px', overflow: 'hidden', border: '.5px solid #bbb' }}>
                            {
                                Object.keys(headers).map((key: string, hi: number) => {
                                    if (hi === 0) return <Fragment key={key}></Fragment>
                                    return (
                                        <div key={hi} style={{ flexDirection: 'row', borderBottom: hi+1 !== Object.keys(headers).length ? '.5px solid #bbb' : 0 }}>
                                            <div style={{ padding: '4px 8px', boxSizing: 'border-box', backgroundColor: '#eee', flex: 1 }}>{ headers[key].text }</div>
                                            <div style={{ padding: '4px 8px', boxSizing: 'border-box', flex: 1 }}>{ headers[key].value(record) }</div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    ))
                }
            </div>
        </>
    )

    return (
        <div className={classes.container}>
            {
                addRecordBox && <AddRecordBox shift={addRecordBox} onSubmit={() => setAddRecordBox(undefined)} onCancel={() => setAddRecordBox(undefined)} />
            }
            <header className={classes.header} style={{flexDirection: 'row'}}>
                {
                    Object.keys(headers).map((key: string, i: number) => {
                        return (
                            <div key={i} style={{ alignItems: 'center', flex: i === 0 ? 0 : headers[key].flex || 1 }}>
                                { headers[key].text }
                            </div>
                        )
                    })
                }    
            </header> 
            <main className={classes.main}>
                {
                    shift.records.map((record: any, ri: number) => (
                        <div className={classes.row} key={ri} style={{flexDirection: 'row'}}>
                            {
                                Object.keys(headers).map((key: string, hi: number) => {
                                    return (
                                        <div className={classes.cell} key={hi} style={{ alignItems: 'center', flex: hi === 0 ? 0 : headers[key].flex || 1 }}>
                                            { headers[key].value(record) }
                                        </div>
                                    )
                                })
                            }
                        </div>
                    ))
                }
                {
                    shift.records.length === 0 &&
                    <div className={classes.titleRow}>אין רישומים למשמרת זו</div>
                }
                {
                    shift.status.opened && !shift.isInShift &&
                    <div className={classes.titleRow}>
                        <button className={classes.addRecordButton} onClick={handleAddRecordOpen}>הוסף רישום</button>
                    </div>
                }
            </main>
            <footer className={classes.footer} style={{ flexDirection: 'row', background: shift.records.length % 2 ? '#efefef' : '#fff' }}>
                {
                    Object.keys(totals).map((key: string, i: number) => (
                        <div className={classes.item} key={i} style={{ width: `${ 100 / Object.keys(totals).length }%`}}>
                            <span>{ totals[key] }</span>
                            { shift[key] || 0 } 
                        </div>
                    ))
                }
            </footer>
        </div>
    )
}

export default ShiftTable