import { gql } from "@apollo/client";

export const LOGIN_MUTATION = gql`
    mutation Login($input: JSON) {
        Login(input: $input) {
            token
        }
    }
`

export const ADD_ACCOUNT_MUTATION = gql`
    mutation AddAccount($input: JSON) {
        AddAccount(input: $input)
    }
`

export const DELETE_ACCOUNT_MUTATION = gql`
    mutation DeleteAccount($id: String) {
        DeleteAccount(id: $id)
    }
`

export const UPDATE_ACCOUNT_MUTATION = gql`
    mutation UpdateAccount($input: JSON) {
        UpdateAccount(input: $input)
    }
`

export const ADD_RECORD_MUTATION = gql`
    mutation AddRecord($input: JSON) {
        AddRecord(input: $input) {
            start
            end
            user {
                id
                firstname
            }
        }
    }
`

export const CLOSE_SHIFT_MUTATION = gql`
    mutation CloseShift($input: JSON) {
        CloseShift(input: $input) 
    }
`

export const SIGN_SHIFT_MUTATION = gql`
    mutation SignShift($input: JSON) {
        SignShift(input: $input)
    }
`

export const SIGN_RECORD_MUTATION = gql`
    mutation SignRecord($input: JSON) {
        SignRecord(input: $input)
    }
`