import { gql } from "@apollo/client";

export const AUTH_QUERY = gql`
    {
        Authenticate {
            token 
            user {
                id
                firstname
                lastname 
                username 
                avatar
                role
            }
        }
    }
`

export const GET_USERS_QUERY = gql`
    {
        getUsers {
            id
            firstname
            lastname 
            username
            avatar
            role
        }
    }
`

export const GET_SHIFT = gql`
    query getShift($date: Date) {
        getShift(date: $date) {
            date
            tip
            isInShift
            records {
                start
                end
                user {
                    id
                    firstname
                    lastname
                    avatar
                }
                signed
            }
            status {
                opened
                closed {
                    user {
                        id
                        firstname
                        lastname
                    }
                    date
                }
                signed {
                    user {
                        id 
                        firstname
                        lastname
                    }
                    date
                }
            }
        }
    }
`

export const GET_USER_SHIFTS = gql`
    {
        getUserShifts {
            date
            tip
            records {
                start 
                end 
                signed
                user {
                    id
                }
            }
            status {
                opened 
                closed {
                    user {
                        firstname
                        lastname 
                    }
                    date 
                }
                signed {
                    user {
                        firstname 
                        lastname 
                    }
                    date 
                }
            }
        }
    }
`