import React from 'react'
import { makeStyles, Theme } from '@material-ui/core'
import { 
    Check as CheckIcon,
    WbSunny as SunIcon,
    NightsStay as MoonIcon
} from '@material-ui/icons'
import clsx from 'clsx'

export enum Kind {
    MORNING = 'בוקר',
    EVENING = 'ערב',
}

export const getKindByDate: ( date?: Date ) => Kind = ( date: Date = new Date() ) => {
    return date.getHours() + ( date.getMinutes() / 60 ) >= 16 ? Kind.EVENING : Kind.MORNING
}

const useStyles = makeStyles((theme: Theme) => ({
    container: {
        position: 'relative',
    },
    mainButton: {
        display: 'flex',
        overflow: 'hidden',
        flex: 1,
        padding: '8px 16px',
        boxSizing: 'border-box',
        textAlign: 'center',
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '40px',
        cursor: 'pointer',
        outline: 'none',
        webkitHightlightTapColor: 'transparent',
        fontSize: '16px',
        color: '#fff',
        border: 0,
        borderRadius: '8px',
        background: '#6C5549',
        '&.opened': {
            borderRadius: '8px 8px 0 0'
        },
        '& span.text': {
            marginLeft: '8px'
        }
    },
    pickerContainer: {
        position: 'absolute',
        top: '40px',
        width: '100%',
    },
    pickerOutline: {
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        zIndex: 1499,
    },
    pickerBox: {
        background: '#fff',
        border: '1px solid #6C5549',
        boxSizing: 'border-box',
        zIndex: 1500,
        borderRadius: '0 0 8px 8px',
        overflow: 'hidden',
        '& button': {
            display: 'flex',
            overflow: 'hidden',
            flex: 1,
            boxSizing: 'border-box',
            textAlign: 'center',
            alignItems: 'center',
            justifyContent: 'space-between',
            minHeight: '40px',
            cursor: 'pointer',
            outline: 'none',
            fontSize: '16px',
            color: '#333',
            border: 0,
            background: 0,
            '& span.text': {
                marginLeft: '.3rem'
            },
            '&:hover': {
                background: '#eee'
            },
            '&:disabled': {
                fontWeight: 500,
                color: '#333',
            }
        }
    }
}))

const KindBar: React.FC<{ kind?: Kind, handleChange: (kind: Kind) => void }> = ({ kind = getKindByDate(new Date()), handleChange }) => {
    const classes = useStyles()
    const [ open, setOpen ] = React.useState<boolean>(false)

    const onSelect = (kind: Kind) => {
        setOpen(false)
        return handleChange(kind)
    }
    return (
        <div className={classes.container}>
            <button className={clsx(classes.mainButton, open && 'opened')} onClick={e => setOpen(true)}>
                <span className="text">{ kind }</span>
                {
                    kind === Kind.MORNING 
                    ? <SunIcon />
                    : <MoonIcon />
                }
            </button>
            {
                open &&
                <div className={classes.pickerContainer}>
                    <div className={classes.pickerOutline} onClick={e => setOpen(false)} />
                    <div className={classes.pickerBox}>
                        <button disabled={kind === Kind.MORNING} onClick={e => onSelect(Kind.MORNING)}>
                            <span className="text">{ Kind.MORNING }</span>
                            { 
                                kind === Kind.MORNING &&
                                <CheckIcon />
                            }
                        </button>
                        <button disabled={kind === Kind.EVENING} onClick={e => onSelect(Kind.EVENING)}>
                            <span className="text">{ Kind.EVENING }</span>
                            { 
                                kind === Kind.EVENING &&
                                <CheckIcon />
                            }
                        </button>
                    </div>
                </div>
            }
        </div>
    )
}

export default KindBar