import React from 'react'
import { makeStyles, Theme, Avatar } from '@material-ui/core'
import { useMutation, useQuery } from '@apollo/client'
import { ADD_ACCOUNT_MUTATION, DELETE_ACCOUNT_MUTATION, UPDATE_ACCOUNT_MUTATION } from '../apollo/mutation'
import { GET_USERS_QUERY } from '../apollo/query'
import { IAuth } from '../router'

interface IState {
    loading?: boolean
    error?: string | null
    success?: string | null
}

interface AddAccountState extends IState {
    input: {
        id: string
        firstname: string
        lastname: string
        [key: string]: string
    }
}

interface AddAccountField {
    name: string
    placeholder: string
}

const useStyles = makeStyles((theme: Theme) => ({
    container: {
        flex: 1,
        padding: '10px 0',
        boxSizing: 'border-box'
    },
    fields: {
        flex: 1,
        flexDirection: 'row',
        flexWrap: 'wrap',
        alignItems: 'center',
        justifyContent: 'center',
        [theme.breakpoints.down(480)]: {
            flexDirection: 'column',
            alignItems: 'flex-start',
            margin: '10px'
        }
    },
    field: {
        '& input': {
            margin: '5px',
            outline: 'none',
            WebkitTapHighlightColor: 'transparent'
        }
    },
    form: {
        display: 'flex',
        flex: 1,
        alignItems: 'center',
        flexDirection: 'column'
    },
    formError: {
        width: '100%',
        color: 'red',
        alignItems: 'center',
        justifyContent: 'center',
    },
    formControls: {
        flexDirection: 'row',
        [theme.breakpoints.down(640)]: {
            width: '100%',
            alignItems: 'center',
            justifyContent: 'center'
        },
        '& button': {
            margin: '0 5px',
        }
    },
    usersList: {
        flex: 1,
        flexDirection: 'row',
        flexWrap: 'wrap',
        padding: '0 10px',
        boxSizing: 'border-box',
        maxHeight: '200px',
        overflowY: 'auto'
    },
    userCardContainer: {
        minWidth: '200px',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '10px 16px',
        boxSizing: 'border-box',
        border: '.5px solid #eee',
        borderRadius: '8px',
        '&:not(last-child)': {
            marginLeft: '8px',
            marginBottom: '8px',
        }
    },
    userCardAvatar: {},
    userCardDetails: {
        flex: 1,
        flexDirection: 'column',
        marginRight: '8px',
        '& h3.fullname': {
            fontSize: '16px',
            fontWeight: 500,
        },
        '& div.username': {
            fontSize: '12px',
            color: '#949494',
            direction: 'ltr',
            textAlign: 'right'
        },
        '& div.item': {
            flexDirection: 'row',
            fontWeight: 500,
            alignItems: 'center',
            '& span': {
                marginRight: '.3rem',
                fontWeight: 400
            }
        }
    },
    searchHeader: {
        padding: '0 10px',
        marginBottom: '10px',
        '& input': {
            outline: 'none',
            padding: '10px 16px',
            boxSizing: 'border-box',
            border: '.5px solid #eee',
            borderRadius: '8px'
        }
    },
    noResultsTitle: {
        flexDirection: 'row',
        '& b': {
            fontWeight: 500
        }
    },
    userSelect: {
        margin: '0 10px',
        padding: '10px 16px',
        boxSizing: 'border-box',
        outline: 'none',
        WebkitTapHighlightColor: 'transparent',
        fontSize: '14px',
        border: '.5px solid #eee',
        borderRadius: '8px',
        WebkitAppearance: 'none',
        MozAppearance: 'none',
        textIndent: '1px',
        textOverflow: ''
    },
}))

interface ITitle {
    text: string
    data: string
}

export const DeleteUser: React.FC<{ onClose: () => void, opened: boolean }> = ({ onClose, opened }) => {
    const classes = useStyles()
    const { loading, error, data } = useQuery(GET_USERS_QUERY, {
        skip: !opened,
    })
    const [ user, setUser ] = React.useState<IAuth['user'] | null>(null)
    const [ doDelete ] = useMutation(DELETE_ACCOUNT_MUTATION)
    const [ deleteError, setError ] = React.useState<string | null>(null)

    if (loading || !data) return <div>רק רגע...</div>

    if (error) return (
        <div>
            <h1>משהו השתבש</h1>
            <p>יש לרענן את הדף ולנסות שוב</p>
        </div>
    )
    
    const { getUsers: users } = data || [] as IAuth['user'][]

    const handleChange = ( e: React.ChangeEvent<HTMLSelectElement> ) => {
        setUser(users.find((user: IAuth['user']) => user.id === e.currentTarget.value) || null)
        setError(null)
    }

    const handleConfirm = () => {
        doDelete({
            variables: {
                id: user?.id
            }
        })
        .then(({ data }) => {
            console.log(data)
        })
        .catch(err => {
            setError(err.message)
        })
    }

    return (
        <div className={classes.container}>
            <select defaultValue={user?.id || 0} onChange={handleChange}>
                <option value={0} disabled>בחר משתמש</option>
                {
                    users.map((user: IAuth['user'], i: number) => (
                        <option key={i} value={user.id}>{ user.firstname } { user.lastname }</option>
                    ))
                }
            </select>
            {
                user && 
                <div>
                    <p>אתה עומד למחוק את <b>{ user.firstname } { user.lastname } לצמיתות, כל הנתונים ימחקו. האם אתה בטוח בפעולה זו?</b></p>
                    <button onClick={handleConfirm}>אישור</button>
                    <button onClick={() => onClose()}>בטל</button>
                </div>
            }
            {
                deleteError &&
                <div>
                    { deleteError }
                </div>
            }
        </div>
    )
}

export const UsersList: React.FC<{ onClose?: () => void, opened: boolean }> = ({ onClose, opened }) => {
    const classes = useStyles()
    const [ filtered, setFiltered ] = React.useState<IAuth['user'][]>([])
    const { loading, error, data, refetch } = useQuery(GET_USERS_QUERY, {
        onCompleted: () => {
            const { getUsers: users } = data || []
            setFiltered(users)
        }
    })
    const [ search, setSearch ] = React.useState<string>('')

    React.useEffect(() => {
        refetch()
    })

    if (loading || !data) return <div>רק רגע...</div>

    if (error) return (
        <div>
            <h1>משהו השתבש</h1>
            <p>יש לרענן את הדף ולנסות שוב</p>
        </div>
    )
    
    const { getUsers: users } = data || [] as IAuth['user'][]

    const handleSearch = ( e: React.ChangeEvent<HTMLInputElement> ) => {
        const value = e.currentTarget.value
        setSearch(value)

        const results = users.filter((user: IAuth['user']) => user.username.startsWith(value) || user.firstname.startsWith(value) || user.lastname.startsWith(value) || user.id.startsWith(value))
        setFiltered(results) 
    }

    const UserCard: React.FC<{ user: IAuth['user'] }> = ({ user }) => {
        return (
            <div className={classes.userCardContainer}>
                <div className={classes.userCardAvatar}>
                    <Avatar src={user.avatar ? `http://${window.location.hostname}:4000${user.avatar}` : ''} />
                </div>
                <div className={classes.userCardDetails}>
                    <h3 className="fullname">{ user.firstname } { user.lastname }</h3>
                    <div className="username">@{ user.username }</div>
                    <div className="item">תז: <span>{ user.id }</span></div>
                    <div className="item">תפקיד: <span>{ user.role }</span></div>
                </div>
            </div>
        )
    } 

    return (
        <div className={classes.container}>
            <header className={classes.searchHeader}>
                <input type="text" placeholder="חפש שם, שם משתמש או מספר תז" value={search} onChange={handleSearch} />
            </header>
            <main className={classes.usersList}>
                {
                    filtered.map((user: IAuth['user'], i: number) => (
                        <div key={i}>
                            <UserCard user={user} />
                        </div>
                    ))
                }
                {
                    filtered.length === 0 && search && <div className={classes.noResultsTitle}>אין תוצאות עבור&nbsp;<b>{ search }</b></div>
                }
            </main>
        </div>
    )
}

export const AddAccount: React.FC<{ onClose?: () => void }> = ({ onClose }) => {
    const classes = useStyles()
    const initialState = {
        input: {
            id: '',
            firstname: '',
            lastname: ''
        }
    }
    const [ doAddAccount ] = useMutation(ADD_ACCOUNT_MUTATION)
    const [ state, setState ] = React.useState<AddAccountState>(initialState)

    const handleClose = ( e?: React.MouseEvent<HTMLButtonElement> ) => {
        onClose?.()
    }

    const handleFocus = ( e: React.FocusEvent<HTMLInputElement> ) => {
        e.currentTarget.readOnly = false
    }

    const handleChange = ( e: React.ChangeEvent<HTMLInputElement> ) => {
        if (e.currentTarget.name === 'id' && isNaN(Number(e.currentTarget.value))) return e.preventDefault() 

        return setState({ ...state, success: null, error: null, input: { ...state.input, [e.currentTarget.name]: e.currentTarget.value.trim() }})
    }

    const handleSubmit = ( e: React.FormEvent<HTMLFormElement> ) => {
        e.preventDefault()
        setState({ ...state, loading: true })

        doAddAccount({
            variables: {
                input: state.input
            }
        })
        .then(({ data }) => {
            console.log(data)
            return setState({ ...initialState, success: `משתמש נוצר בהצלחה`})
        })
        .catch(err => {
            return setState({ ...state, loading: false, error: err.message })
        })
    }

    const items: AddAccountField[] = [
        {
            name: 'id',
            placeholder: 'תעודת זהות'
        },
        {
            name: 'firstname',
            placeholder: 'שם פרטי'
        },
        {
            name: 'lastname',
            placeholder: 'שם משפחה'
        }
    ]

    return (
        <div className={classes.container}>
            <form onSubmit={handleSubmit} className={classes.form} autoComplete="off">
                <input style={{display: 'none'}} />
                <div className={classes.fields}>
                    {
                        items.map((field: AddAccountField, i: number) => {
                            return (
                                <div key={i} className={classes.field}>
                                    <input readOnly type="text" { ...field } id={field.name} onFocus={handleFocus} onChange={handleChange} value={state.input[field.name]} />
                                </div>
                            )
                        })
                    }
                    <div className={classes.formControls}>
                        <button type="submit" disabled={Boolean(state.loading || state.error || !state.input.id || !state.input.firstname || !state.input.lastname)}>הוסף</button>
                        <button onClick={handleClose}>בטל</button>
                    </div>
                    {
                        state.error &&
                        <div className={classes.formError}>
                            { state.error }
                        </div>
                    }
                    {
                        state.success &&
                        <div className={classes.formError} style={{ color: 'green' }}>
                            { state.success }
                        </div>
                    }
                </div>
            </form>
        </div>
    )
}

interface UpdateAccountState {
    account: {
        update: IAuth['user'],
        id?: string
    }
    loading: boolean
    response?: string
    changed: boolean
    error: string | null
}

export const UpdateAccount: React.FC<{ onClose?: () => void, opened: boolean }> = ({ onClose, opened }) => {
    const classes = useStyles()
    const [ loaded, setLoaded ] = React.useState<boolean>(false)
    const { loading, error, data, refetch: refetchUsers } = useQuery(GET_USERS_QUERY, {
        skip: !loaded,
    })
    const initialState = {
        account: {
            update: {} as IAuth['user']
        },
        loading: false,
        response: '',
        changed: false,
        error: null
    }
    const [ state, setState ] = React.useState<UpdateAccountState>(initialState)
    const [ doUpdateAccount ] = useMutation(UPDATE_ACCOUNT_MUTATION)

    React.useEffect(() => {
        setLoaded(true)
    }, [])

    if (loading || !data) return <div>רק רגע...</div>

    if (error) return (
        <div>
            <h1>משהו השתבש</h1>
            <p>יש לרענן את הדף ולנסות שוב</p>
        </div>
    )

    const handleClose = ( e?: React.MouseEvent<HTMLButtonElement> ) => {
        onClose?.()
    }

    const handleChange = ( e: React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLSelectElement> ) => {
        const account = users.find((user: IAuth['user']) => user.id === state.account.id) || null
        let changed = true 
        if (account && account[e.currentTarget.name] === e.currentTarget.value) {
            changed = false
        }

        setState({ ...state, changed, account: { ...state.account, update: {...state.account.update, [e.currentTarget.name]: e.currentTarget.value } }})
    }   

    const handleAccountChoose = ( e: React.ChangeEvent<HTMLSelectElement> ) => {
        const account = users.find((user: IAuth['user']) => user.id === e.currentTarget.value) || null
        setState({ ...initialState, account: { update: account, id: account.id } })
    }

    const handleSubmit = ( e: React.FormEvent<HTMLFormElement> ) => {
        e.preventDefault()

        setState({ ...state, loading: true })
        console.log(state.account)
        const { id, firstname, lastname, role } = state.account.update
        const idToUpdate = state.account.id

        doUpdateAccount({
            variables: {
                input: { target: idToUpdate, id, firstname, lastname, role }
            }
        })
        .then(({ data }) => {
            const { status, response } = data.UpdateAccount
            refetchUsers()
            if (status === 200) return setState({ ...state, response })
        })
        .catch(err => {
            console.log(err)
            setState({ ...state, error: err.message })
        })
    }

    const items: AddAccountField[] = [
        {
            name: 'id',
            placeholder: 'תעודת זהות'
        },
        {
            name: 'firstname',
            placeholder: 'שם פרטי'
        },
        {
            name: 'lastname',
            placeholder: 'שם משפחה'
        }
    ]

    const { getUsers: users } = data || [] as IAuth['user'][]
    
    return (
        <div className={classes.container}>
            <select className={classes.userSelect} defaultValue="0" onSelect={e => e.currentTarget.blur()} onChange={handleAccountChoose}>
                <option value="0" disabled>בחר משתמש</option>
                {
                    users.map((user: IAuth['user'], i: number) => (
                        <option key={i} value={user.id}>{ user.firstname } { user.lastname }</option>
                    ))
                }
            </select>
            {
                Object.keys(state.account.update).length > 0 &&
                <form onSubmit={handleSubmit} className={classes.form} autoComplete="off">
                    <input style={{display: 'none'}} />
                    <div className={classes.fields}>
                        {
                            items.map((field: AddAccountField, i: number) => {
                                const value = state.account.update[field.name]
                                return (
                                    <div key={i} className={classes.field}>
                                        <span>{ field.placeholder }</span>
                                        <input type="text" value={value} name={field.name} placeholder={field.placeholder} onChange={handleChange} />
                                    </div>
                                )
                            })
                        }
                        <div className={classes.field}>
                            <span>תפקיד</span>
                            <select name="role" value={state.account.update.role} onChange={handleChange}>
                                <option value={0} disabled>בחר תפקיד</option>
                                <option value="מלצר">מלצר</option>
                                <option value="מנהל">מנהל</option>
                                <option value="מפתח">מפתח</option>
                            </select>
                        </div>
                    </div>
                    <div style={{ flexDirection: 'row', justifyContent: 'flex-end'}}>
                        <button disabled={!state.changed} type="submit">עדכן</button>
                        <button type="button" onClick={handleClose}>בטל</button>
                    </div>
                </form>
            }
            {
                state.response && 
                <div>
                    { state.response }
                </div>
            }
            {
                state.error &&
                <div>
                    { state.error}
                </div>
            }
        </div>
    )
}