import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { split, HttpLink, ApolloProvider, ApolloClient, InMemoryCache } from '@apollo/client'
import { getMainDefinition } from '@apollo/client/utilities'
import { WebSocketLink } from '@apollo/client/link/ws'

const PORT: string | number = process.env.PORT || 4000
const API_URI: string = process.env.NODE_ENV === 'production' ? `http://${window.location.hostname}:4000/graphql` : `http://${window.location.hostname}:${PORT}/graphql`
const WS_URI: string = process.env.NODE_ENV === 'production' ? `ws://${window.location.hostname}:4000/graphql` : `ws://${window.location.hostname}:${PORT}/graphql`
const TOKEN: string = localStorage.getItem('token') || ''

const httpLink: HttpLink = new HttpLink({
  uri: API_URI,
  headers: {
    token: TOKEN
  }
})

const wsLink: WebSocketLink = new WebSocketLink({
  uri: WS_URI,
  options: {
    connectionParams: {
      token: TOKEN
    }
  }
})

const link = split(
  ({ query }) => {
    const definition = getMainDefinition(query)
    return (
      definition.kind === 'OperationDefinition' &&
      definition.operation === 'subscription'
    )
  },
  wsLink,
  httpLink
)

const client = new ApolloClient({
  link,
  cache: new InMemoryCache()
})

ReactDOM.render(
  <ApolloProvider client={client}>
    <App />
  </ApolloProvider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
